
import { hasPermission } from "@/core/helpers/functions";
import { defineComponent, ref } from "vue";
export default defineComponent({
  name: "cardCompany",
  props: {
    widgetClasses: String,
    color: String,
    item: Object
  },
  setup() {
    return {
      update_companies: ref(hasPermission("companies: update")),
      delete_companies: ref(hasPermission("companies: delete"))
    };
  }
});
